@layer components {
    .btn {
        @apply py-2 px-4 font-semibold rounded-lg shadow-md justify-self-end focus:outline-none;
    }
    .btn-green {
        @apply text-white bg-green-500 hover:bg-green-700;
    }

    .btn-red {
        @apply text-white bg-red-500 hover:bg-red-700;
    }
}